.hero {
    position: relative;
    background: #FFB8CA;
    background-repeat: no-repeat;
    letter-spacing: normal;

    z-index: 1;
    
    @include less-tab {
        height: 502px;
        margin-top: -62px;
    }

    @include tab {
        height: 399px;
        margin-top: -87px;
    }

    @include desk {
        height: 665px;
    }
}

.hero__wrap {
    position: relative;
    background-repeat: no-repeat;

    margin-left: auto;
    margin-right: auto;

    @media (max-width: 479px) {
        height: 502px;
        background-image: url("../images/hero/icecream-mob-1x.png"), url("../images/hero/Ellipse-mob-1x.png");
        background-position: right 32px top 92px , right 0px top 83px;
        background-size: 171px 410px, 284px 346px;

        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {   
            background-image: url("../images/hero/icecream-mob-2x.png"), url("../images/hero/Ellipse-mob-2x.png");    

        }
    }

    @include mobile {
        width: 480px;
        height: 502px;
        background-image: url("../images/hero/icecream-mob-1x.png"), url("../images/hero/Ellipse-mob-1x.png");
        background-position: right 32px top 92px , right 0px top 83px;
        background-size: 171px 410px, 284px 346px;
       
        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
                
                background-image: url("../images/hero/icecream-mob-2x.png"), url("../images/hero/Ellipse-mob-2x.png");
                
        }
    }

    @include tab {
        width: 768px;
        height: 399px;
        background-image: url("../images/hero/icecream-tab-1x.png"), url("../images/hero/lady-tab-1x.png"), url("../images/hero/Ellipse-tab-1x.png"), 
        url("../images/hero/milk-tab-1x.png");
        background-position: right 248px top 63px, right 34px top 266px, right 138px top 31px, left 34px top 245px;
        background-size: 143px 336px, 168px 133px, 324px 324px, 124px 126px;

        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url("../images/hero/icecream-tab-2x.png"), url("../images/hero/lady-tab-2x.png"), url("../images/hero/Ellipse-tab-2x.png"), 
            url("../images/hero/milk-tab-2x.png");
           
        }
    }

    @include desk {
        width: 1280px;
        height: 665px;
        background-image: url("../images/hero/icecream-desk-1x.png"), url("../images/hero/lady-desk-1x.png"), url("../images/hero/Ellipse-desk-1x.png"), 
        url("../images/hero/milk-desk-1x.png");
        background-position: right 450px top 108px, right 109px top 433px, right 267px top 52px, left 77px top 429px;
        background-size: 236px 557px, 293px 232px, 538px 538px, 188px 191px;

        @media (min-device-pixel-ratio: 2),
        (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url("../images/hero/icecream-desk-2x.png"), url("../images/hero/lady-desk-2x.png"), url("../images/hero/Ellipse-desk-2x.png"), 
            url("../images/hero/milk-desk-2x.png");

        }
    }
}

.hero__block {
   display: block;

   @include less-tab {
        display: none;
    }

    @include tab {
        width: 114px;
        margin-left: 134px;
    }
    @include desk {
        width: 200px;
        margin-left: 180px;
    }
}

.hero__text {
    font-family: "DM Sans";
    color: #ffffff;

    @include tab {
        font-size: 8px;
        line-height: calc(12 / 8);
    }

    @include desk {
        font-size: 14px;
        line-height: calc(20 / 14);
    }

}

.hero__button {
    display: inline-flex;
    height: 26px;
    width: 26px;
    border-radius: 50%;
    border: 1px solid #ffffff;

    justify-content: center;
    align-items: center;

    background-color: #ffffff;
    // background-image: url(../images/hero/arrow-right.png);
    // background-repeat: no-repeat;
    // background-position: center;
    // background-size: contain;
    margin-top: 20px;

    cursor: pointer;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include less-desk {
        height: 18px;
        width: 18px;
        margin-top: 7px;
    }

    &:hover,
    &:focus {
        background-color: #ECEBEB;
        border-color: #D41443;
        box-shadow: 1px 4px 2px rgba(212, 20, 67, 0.1), 
        1px 2px 3px rgba(212, 20, 67, 0.08), 
        1px 3px 3px rgba(212, 20, 67, 0.12);
    }
}

.hero__meta {
    @include less-tab {
        width: 218px;

        padding-top: 193px;
    }

    @include tab {
        width: 162px;

        padding-top: 8px;
        margin-bottom: 66px;
        margin-right: 494px;
    }

    @include desk {  
        width: 277px;
    
        padding-top: 15px;
        margin-right: 714px;
        margin-bottom: 112px;    
    }
}

.hero__title {
    font-family: 'Titan One';
    color: #ffffff;
    font-size: 26px;
    line-height: calc(30 / 26);
    letter-spacing: 0.04em;
    text-transform: uppercase;

    margin-bottom: 20px;

    @include less-tab {
        font-size: 26px;
        line-height: calc(30 / 26);
    }
    @include tab {
        font-size: 22px;
        line-height: calc(25 / 22);
    }

    @include desk {
        
        font-style: normal;
        font-weight: 400;
        font-size: 38px;
        line-height: 44px;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        margin-bottom: 25px;
    }
}

.hero__title--dark {
    color: #D41443;

    letter-spacing: 0.04em;
    text-transform: uppercase;

    @include less-tab {
        font-size: 48px;
        line-height: calc(46 / 48);
    }
    
    @include tab {
        font-size: 34px;
        line-height: calc(22 / 25);
    }

    @include desk {
        font-size: 58px;
    }
}

.hero__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;

    font-family: "DM Sans";
    font-size: 8px;
    line-height: 10px;
    
    color: #D41443;
    background-color: #ffffff;
    border-radius: 20px;
    border: 1px solid #ffffff;
    // border-width: 1px;
    // border-style: solid;
    // border-color: #ffffff;

    height: 24px;
    width: 82px;

    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
                box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    @include desk {
        height: 40px;
        width: 138px;

        font-size: 14px;
        line-height: calc(18 / 14);
        
    }

    &:hover,
    &:focus {
        background-color: #ECEBEB;
       
        border-color:  #D41443;
        box-shadow: 1px 4px 2px rgba(212, 20, 67, 0.1), 
        1px 2px 3px rgba(212, 20, 67, 0.08), 
        1px 3px 3px rgba(212, 20, 67, 0.12);
    }
}

.hero__link--dark {
    color: #ffffff;
    background-color: #D41443;
    border: 1px solid #D41443;

   
    transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1),
                // border-color 250ms cubic-bezie(0.4, 0, 0.2, 1),
                background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
                // color 250ms cubic-bezier(0.4, 0, 0.2, 1),


    @include less-desk {
        width: 62px;

        margin-right: 8;
    }

    @include desk {
        width: 104px;

        margin-right: 15px;
    }

    &:hover,
    &:focus {
        // color: #D41443;
        background-color: #D41443;
        // border-color: #ffffff;
        box-shadow: 1px 4px 2px rgba(212, 20, 67, 0.1), 
        1px 2px 3px rgba(212, 20, 67, 0.08), 
        1px 3px 3px rgba(212, 20, 67, 0.12);
    }
}

.figures {
    font-family: "Titan One";

    @include less-tab {
        display: none;
    }
    @include tab {
        top: 94px;
        right: 34px;
    }
    @include desk {
        top: 156px;
        right: 97px;
    }
}

.hero__figure {
    position: relative;
    z-index: 3;

    display: block;
    width: 43px;
    height: 44px;

    color: #D41443;
    font-size: 28px;
    line-height: calc(28 / 32);

    padding-top: 7px;
    padding-left: 2px;

    &:nth-child(1) {
        margin-bottom: 13px;
    }

    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
        display: block;
        height: 16px;
        width: 16px;
        

        background-color: #ffffff;
        border-radius: 50%;

        @include desk {
            height: 30px;
            width: 30px;
        }
    }

    &::after {
        content: "cafes";
        color: #ffffff;
        position: absolute;
        bottom: 0;
        right: 10px;
        
        font-family: "DM Sans";
        font-size: 8px;

        @include desk {
            right: 23px;
            
            font-size: 14px;
           
        }
    }

    @include desk {
        width: 74px;
        height: 77px;

        font-size: 46px;
        line-height: calc(53 / 46);
    }
}


.hero__figure--truck {
    width: 51px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 8px;
        z-index: -1;
        display: block;
        height: 16px;
        width: 16px;

        @include desk {
            height: 30px;
            width: 30px;

            right: 18px;
        }
    }

    &::after {
        content: "food trucks";
        width: 45px;
        color: #ffffff;
        position: absolute;
        bottom: 0;
        right: 14px;
        
        font-family: "DM Sans";
        font-size: 8px;

        @include desk {
            right: 35px;
            width: 76px;
            font-size: 14px;
            line-height: calc(18 / 14);
        }
    }

    @include desk {
        width: 92px;
        height: 76px;
    }
}

.hero__info {
    display: flex;

    @include tab {
        padding-top: 94px;
    }

    @include desk {
        padding-top: 156px;
    }
    
}